import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_SET_ROW_HEADER = Symbol('TABLE_SET_ROW_HEADER');

export function setTableRowHeader(node: Table) {
  return {
    type: TABLE_SET_ROW_HEADER,
    payload: {
      node,
    },
  };
}

interface TableSetTableRowHeaderPayload {
  node: Table;
}

interface TableSetTableRowHeaderAction {
  type: typeof TABLE_SET_ROW_HEADER;
  payload: TableSetTableRowHeaderPayload;
}

export function isTableSetTableRowHeaderAction(
  action: Action,
): action is TableSetTableRowHeaderAction {
  return action.type === TABLE_SET_ROW_HEADER;
}
